@font-face {
  font-family: 'Panton';
  src: url('../fonts/Panton-Regular.woff2') format('woff2'),
  url('../fonts/Panton-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Panton';
  src: url('../fonts/Panton-Bold.woff2') format('woff2'),
  url('../fonts/Panton-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Panton';
  src: url('../fonts/Panton-Black.woff2') format('woff2'),
  url('../fonts/Panton-Black.woff') format('woff');
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

figure {
  margin: 0;
}

body {
  margin: 0;
  font-size: 16px;
  line-height: 1.625;
  font-family: 'Panton';
  font-weight: normal;
  background-color: var(--color-white);

  &.fancybox-active {
    margin-right: 17px !important;
  }

  @include _1200 {
    background: linear-gradient(to bottom, #baf0e9 0%, #fff 100%);
  }
}

img {
  max-width: 100%;
  vertical-align: middle;
}

svg {
  overflow: hidden;
  vertical-align: middle;
  transition: all .3s ease;
}

a {
  color: inherit;
  text-decoration: none;
  background-color: transparent;
  transition: all .3s ease;

  &.link {
    color: var(--color-primary);

    &:hover {
      color: var(--color-dark);
    }
  }
  &:hover {
    color: var(--color-primary);
  }
}

.wrapper {
  overflow: hidden;

  @include _767 {
    padding-top: 80px;
  }
}


.container {
  min-width: 320px;
}

.modal-body.fancybox-content {
  max-width: 1140px;
}

.modal-btn {
  font-weight: 700;
  border: 2px solid #ef3139;
  border-radius: 15px;
  padding: 3px 20px;
  margin-top: 20px;
  display: inline-block;

  &:hover {
    color: #fff;
    background: #ef3139;
  }
}

.mobile-btn {
  @include _767 {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
    padding-right: 20px;

    .btn-primary {
      padding: 5px 15px;
      font-size: 12px;
    }
  }
}

.form-errors {
  display: none !important;
}

.form-success {
  position: absolute;
  height: 100%;
  font-weight: 700;
  color: #00c3b3;
  font-size: 26px;
  top: 0;
  width: 100%;
  display: none;
  align-items: center;
  justify-content: center;
  background: #effbf9;

  &.active {
    display: flex;
  }
}

ul {
  list-style: none;
  padding: 0;

  &.def {
    padding-left: 40px;
    list-style: disc;
  }
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.is-hidden {
  display: none !important;
}

.is-overflow-hidden {
  overflow: hidden !important;
}

.w-100 {
  width: 100%;
}

section {
}

.hover-shine .shine-block::after {
  position: absolute;
  top: 0;
  left: -75%;
  z-index: 2;
  display: block;
  content: '';
  width: 50%;
  height: 100%;
  pointer-events: none;
  background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
  background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
  -webkit-transform: skewX(-25deg);
  transform: skewX(-25deg);
}
.hover-shine .shine-block:hover::after {
  animation: shine 1s;
}
@keyframes shine {
  100% {
    left: 125%;
  }
}

.for-mobile {
  display: none;

  @include _991 {
    display: block;
  }
}
.for-desctop {
  display: block;

  @include _991 {
    display: none;
  }
}

.text-color-primary {
  color: var(--color-primary);
}
svg.text-color-primary {
  fill: var(--color-primary) !important;
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.border-bottom-primary {
  border-bottom: 1px solid var(--background-gray);
  position: relative;
  padding-bottom: 14px;

  &:before {
    content: '';
    background: var(--color-primary);
    height: 3px;
    width: 90px;
    position: absolute;
    bottom: -2px;
  }
}

.fancybox-content {
  border-radius: 3px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.hover-primary:hover {
  color: var(--color-primary);

  &:after {
    color: var(--color-primary);
  }
}
.hover-white:hover {
  color: var(--color-white);

  &:after {
    color: var(--color-white);
  }
}
.hover-dark:hover {
  color: var(--color-dark);

  &:after {
    color: var(--color-dark);
  }
}

.fs-10 {
  font-size: 10px;
}
.fs-13 {
  font-size: 13px;
}

.fs-18 {
  font-size: 18px;
}

.dropdown-list {
  position: relative;
  &-item {
    position: absolute;
    top: 100%;
    left: 0;
    margin: 0;
    z-index: 60;
    flex-direction: column;
    min-width: 170px;
    padding-top: 10px;
    border-radius:0 0 3px 3px;
    background: #fff;
    border-top: 3px solid var(--color-primary);
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, .1);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    transform: translate(0, 10px);

    li {

      a {
        padding: 10px;
        border-bottom: 1px solid var(--background-lightgray);
      }

      &:last-child {

        a {
          margin-bottom: 0;
          border-bottom: 0;
        }
      }
    }
  }

  &:hover {
    .dropdown-list-item {
      opacity: 1;
      visibility: visible;
      transform: translate(0, 0);
    }
  }

}

.section-title {
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;

  .section-title__block {
    text-align: center;
    margin-bottom: 35px;
    color: var(--color-primary);
    line-height: 35px;
    position: relative;
    border:4px solid var(--color-primary);
    background: var(--color-white);
    border-radius: 15px;
    font-size: 30px;
    padding: 14px 50px;
    max-width: 800px;

    @include _1200 {
      font-size: 22px;
      position: relative;
      line-height: 24px;

      .section-title__number {
        top: -3px;
        left: -23px;
        width: 49px;
        height: 70px;
        background-size: contain;
        font-size: 37px;
        line-height: 86px;
      }
    }
    @include _767 {
      padding: 10px 10px 10px 40px;
      text-align: left;
    }
  }

  img {
    margin: -35px 0 0 15px;
    width: 160px;

    @include _767 {
      width: 80px;
    }
  }
}

.section-title__number {
  position: absolute;
  top: -5px;
  left: -32px;
  color: var(--color-primary);
  background: url("../img/title-after.png") no-repeat;
  font-size: 50px;
  font-weight: 700;
  width: 65px;
  height: 94px;
  line-height: 107px;
  text-align: center;
}

.fwbold {
  font-weight: 700;
}

.main-content {
  background: linear-gradient(to bottom, #baf0e9 0%, #fff 100%);

  @include _1200 {
    background: none;
    position: relative;
  }
}

.mobile-line-750 {
  display: none;

  @include _1200 {
    display: block;
    position: absolute;
    top: -24px;
    left: -46px;
    width: 844px;
  }

  @include _767 {
    display: none;
  }
}
.mobile-line-480 {
  display: none;

  @include _767 {
    position: absolute;
    top: -24px;
    left: 50%;
    display: block;
    width: 473px;
    transform: translate(-50%, 0);
  }
}

.color-blue {
  color:#1a3668;
}
.color-red {
  color:#e5004f;
}

.section-content {
  z-index: 10;
}

.figure-heart {
  position: absolute;
  right: -340px;
  top:50%;
  transform: translateY(-36%);
  background: url("../img/figure-heart.png") no-repeat;
  width: 695px;
  height: 678px;

  @include _1200 {
    z-index: -1;
  }

  @include _767 {
    display: none;
  }
}